<template>
  <div id="myGoods">
    <div class="yoya">yoya尚品汇</div>
<div class="ss"  style="height:34px;position:absolute;top:0;right:0">
    <input ref="key" v-model="keyword" 
   style="height:34px;text-indent:10px;"
    size="mini" placeholder="输入关键字搜索" />
    <el-button type="primary" @click="onSubmit">查询</el-button>
</div>


    <el-table
      :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)"
      style="width: 100%"
    >
      <el-table-column type="index" label="序号" width="50"></el-table-column>

      <i class="el-icon-caret-bottom"></i>

      <!-- <router-link :to="{name:'home'}"> -->
      <el-table-column label="名称" prop="title" width="150">
        <template slot-scope="scope">
          <el-popover placement="top-start" title="商品详情" width="400" trigger="hover">
            <el-card class="box-card">
              <ul>
                <li>
                  <div>
                    <!-- <img :src='scope.row.imageUrl'> -->
                  </div>
                  <span>商品名称：{{scope.row.title}}</span>
                </li>
                <li>
                  <span>商品均价：￥{{scope.row.avgprice}}</span>
                </li>
                <li>
                  <span>商家地址：{{scope.row.address}}</span>
                </li>
                <li>
                  <span>商品类别：{{scope.row.showType}}</span>
                </li>
                <li></li>
                <li></li>
              </ul>
            </el-card>

            <span slot="reference" class="todetail">{{scope.row.title}}</span>
          </el-popover>
        </template>
      </el-table-column>
      <!-- </router-link> -->

      <el-table-column label="地址" prop="address" width="180"></el-table-column>

      <el-table-column label="经度" prop="longitude" width="90">
        <template slot-scope="scope">
          <span>
            {{scope.row.longitude.toFixed(2)}} E
            <sup>o</sup>
          </span>
        </template>
      </el-table-column>

      <el-table-column label="纬度" prop="latitude" width="80">
        <template slot-scope="scope">
          <span>
            {{scope.row.latitude.toFixed(2)}} N
            <sup>o</sup>
          </span>
        </template>
      </el-table-column>

      <el-table-column label="最低价" prop="lowestprice" width="80" @click="getinfo">
        <template slot-scope="scope">
          <span>￥{{scope.row.lowestprice.toFixed(2)}}</span>
        </template>
      </el-table-column>

      <el-table-column label="平均价" prop="avgprice" width="80"></el-table-column>

      <el-table-column label="评分" prop="avgscore" width="80"></el-table-column>

      <el-table-column label="类型" prop="showType" width="80"></el-table-column>

      <el-table-column label="销量" prop="historyCouponCount" width="80"></el-table-column>

      <el-table-column label="评价数量" prop="comments" width="80"></el-table-column>
    </el-table>
    <template>
      <div class="sorta">
        <i class="el-icon-caret-bottom" @click="jianga"></i>
        <i class="el-icon-caret-top" @click="shena"></i>
      </div>
    </template>

    <template>
      <div class="sortb">
        <i class="el-icon-caret-bottom" @click="jiangb"></i>
        <i class="el-icon-caret-top" @click="shenb"></i>
      </div>
    </template>

    <template>
      <div class="sortc">
        <i class="el-icon-caret-bottom" @click="jiangc"></i>
        <i class="el-icon-caret-top" @click="shenc"></i>
      </div>
    </template>

    <template>
      <div class="sortd">
        <i class="el-icon-caret-bottom" @click="jiangd"></i>
        <i class="el-icon-caret-top" @click="shend"></i>
      </div>
    </template>

    <template>
      <div class="sorte">
        <i class="el-icon-caret-bottom" @click="jiange"></i>
        <i class="el-icon-caret-top" @click="shene"></i>
      </div>
    </template>

    <template>
      <div class="paginationBox">
        <div class="block">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="[5, 10, 20, 40]"
            :page-size="pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="tableData.length"
          ></el-pagination>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [],
      currentPage: 1, //初始页
      pagesize: 10, //    每页的数据
      keyword: "",
      search: "",
      rank: 1,
      depend: "",
      visible: false,
      prop: false,
      form: {},
      dialogTableVisible: false,
      dialogFormVisible: false,
      value1: "",
      formLabelWidth: "120px"
    };
  },
  methods: {
    // 初始页currentPage、初始每页数据数pagesize和数据data
    handleSizeChange: function(size) {
      this.pagesize = size;
      // console.log(this.pagesize); //每页下拉显示数据
    },
    handleCurrentChange: function(currentPage) {
      this.currentPage = currentPage;
      // console.log(this.currentPage); //点击第几页
    },
    handleClose(done) {
      this.$confirm("确认关闭？")
        .then(_ => {
          done();
        })
        .catch(_ => {});
    },
    getinfo() {
      //   console.log(this.rank);
      //   console.log(this.depend);
      var word=this.$refs.key.value;
      
      this.$axios
        .get("/vue/mygoodslist", {
          params: {
            keyword: word,
            rank: this.rank,
            depend: this.depend
          }
        })
        .then(res => {
          //   console.log(res);
          this.tableData = res.data.result;
           if (this.tableData.length) {
            this.$refs.key.value = "";
          }
        });
    },

    // 排序
    jianga() {
      // console.log("降序")
      this.rank = -1;
      this.depend = "lowestprice";
      console.log(this.rank);
      console.log(this.depend);
      this.getinfo();
    },
    shena() {
      this.rank = 1;
      this.depend = "lowestprice";
      this.getinfo();
    },

    jiangb() {
      this.rank = -1;
      this.depend = "avgprice";
      this.getinfo();
    },
    shenb() {
      this.rank = 1;
      this.depend = "avgprice";
      this.getinfo();
    },
    jiangc() {
      this.rank = -1;
      this.depend = "avgscore";
      this.getinfo();
    },
    shenc() {
      this.rank = 1;
      this.depend = "avgscore";
      this.getinfo();
    },
    jiangd() {
      this.rank = -1;
      this.depend = "historyCouponCount";
      this.getinfo();
    },
    shend() {
      this.rank = 1;
      this.depend = "historyCouponCount";
      this.getinfo();
    },
    jiange() {
      this.rank = -1;
      this.depend = "comments";
      this.getinfo();
    },
    shene() {
      this.rank = 1;
      this.depend = "comments";
      this.getinfo();
    },
    onSubmit() {
        console.log(242342)
      this.getinfo();
    }
  },
  mounted() {
    console.log(this.rank);
  },
  created() {
    this.$axios
      .get("/vue/mygoodslist", {
        params: {
          keyword: "",
          rank: this.rank,
          depend: this.depend
        }
      })
      .then(res => {
        this.tableData = res.data.result;
      });
  }
};
</script>

<style lang="scss" scoped>
.todetail {
  text-decoration: underline;
  color: darkcyan;
}
.box-card {
  background: rgba(168, 45, 152, 0.133);
}
.sorta {
  width: 30px;
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 66px;
  right: 420px;
}
.sortc {
  width: 30px;
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 66px;
  right: 275px;
}
.sortb {
  width: 30px;
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 66px;
  right: 344px;
}
.sortd {
  width: 30px;
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 66px;
  right: 115px;
}
.sorte {
  width: 30px;
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 66px;
  right: 10px;
}
.yoya {
  font-family: "Hiragino Sans GB";
  color: #000;
  font-weight: bold;
  font-size: 30px;
  margin: 10px;
}
.el-date-editor.el-input,
.el-date-editor.el-input__inner {
  width: 100%;
}
#myGoods{
    margin-bottom: 80px
}
</style>